@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.red-border {
  border: 1px solid red;
}

.green-border {
  border: 1px solid green;
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    -webkit-animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
    width: 180px;
    height: 90px;
    padding: 40px;
  }
}

.App-header {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.oi {
  background-color: violet;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

body {
  background-color: #355DD7;
}

.login-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  box-shadow: 4px 4px 10px black;
  border-radius: 8px;
  width: 450px;
  height: 450px;
  padding: 0 100px 100px 100px;
  background-color: whitesmoke;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active
{
 -webkit-box-shadow: 0 0 0 30px whitesmoke inset !important;
}
.questions-content {
  display: flex;
  flex-direction: column;
  margin: 5px 20px 2px 20px;
  padding-top: 4px;
  padding-bottom: 3px;
}

.cardEndQuestions {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.geral {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  box-shadow: 4px 4px 10px black;
  border-radius: 8px;
  width: 450px;
  height: 450px;
  padding: 0 150px 170px 150px;
  background-color: whitesmoke;
}

.secondsEndCategory {
  display: flex;
  justify-content: space-between;
  text-shadow: 2px 2px 5px;
  font-size: 10px;
}

.asking {
  text-shadow: 2px 2px 5px #00BFFF;
  font-size: 20px;
}

.seconds {
  margin-right: 0px;
  color: #f00;
  font-size:x-large;
}

.w-answer{
  margin-bottom: 3px;
  background-color: #1e0bd0;
  color: #fff;
  border:1px solid #000000;
  border-radius:4px;
  display:inline-block;
  cursor:pointer;
  font-family:Verdana;
  font-weight:bold;
  font-size:15px;
  padding:10px 14px;
  text-decoration:none;
}

.c-answer {
  margin-bottom: 3px;
  background-color: #1e0bd0;
  color: #fff;
  border:1px solid #000000;
  border-radius:4px;
  display:inline-block;
  cursor:pointer;
  font-family:Verdana;
  font-weight:bold;
  font-size:15px;
  padding:10px 14px;
  text-decoration:none;
}

.headerGame {
  display: flex;
  justify-content: space-between;
  text-shadow: 2px 2px 10px;
  font-size: x-large;
  font-style: italic;
  margin-bottom: 10px;
  align-items: flex-end;
}

.headerGame h5 {
  color: #1e0bd0;
  margin-left: 10px;
}

.btn-proximo {
  display: block;
  margin: 5px auto 2px auto;
}
.button-play-again {
  padding: 10px;
}

.button-ranking {
  padding: 10px;
}

.feedback-content {
  align-items: center;
  background-color: whitesmoke;
  border-radius: 8px;
  box-shadow: 4px 4px 10px black;
  display: flex;
  flex-direction: column;
  height: 550px;
  justify-content: space-between;
  padding: 0 100px 100px;
  width: 450px;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active
{
  -webkit-box-shadow: 0 0 0 30px whitesmoke inset !important;
}

* {
  font-family: 'Montserrat', sans-serif;
  font-weight: 800;

}

.section-ranking {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.title {
  color: whitesmoke;
}

.ranking-list {
  background-color: whitesmoke;
  list-style: none;
  padding: 0px;
  width: 80%;
  margin-bottom: 15px;
  border-radius: 8px;
  box-shadow: 4px 4px 10px black;
  margin-bottom: 35px;
  align-self: center;
}

.li-ranking {
  padding: 20px 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  border-radius: 8px;
}

.li-header{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  border: 2px solid black;
  background-color: #C51D62;
  color: white;
}

.li-ranking:nth-child(even) {
  background-color: silver;
  margin: 0;
}

#user-pic {
  border-radius: 50%;
  width: 90px;
  height: 90px;
}

#user-name {
  width: 200px;
}

.score {
  margin-bottom: 0px;
}

.go-back-btn {
  margin: auto;
  width: 10vw;
  align-self: center;
  bottom: 10px;
}

